<template>
  <div>
    <!-- 头部标题 -->
    <div class="header">菜品推荐管理</div>
    <!-- 内容区域 -->
    <div class="container">
      <!-- 搜搜区域 -->
      <div class="searchBox">
        <input
          class="searchInput"
          v-model="search.keyword"
          type="text"
          placeholder="请输入查找内容"
        />

        <button type="button" class="searchBtn" @click="searchFn">搜索</button>
        <div class="addBtn">
          <el-select v-model="Type" placeholder="请选择" size="mini">
            <el-option value="店铺推荐"> </el-option>
            <el-option value="前台展示"> </el-option>
          </el-select>
        </div>
      </div>
      <!-- 展示列表区 -->
      <div class="listBox">
        <!-- 轮列表 -->
        <div class="list">
          <template>
            <el-table :data="tableData" border stripe style="width: 100%">
              <el-table-column
                prop="did"
                label="菜品ID"
                width="120"
                align="center"
              >
              </el-table-column>
              <el-table-column
                prop="sid"
                label="店铺ID"
                width="120"
                align="center"
              >
              </el-table-column>
              <el-table-column label="菜品图片" width="150" align="center">
                <template v-slot="{ row }">
                  <img :src="row.dimage" style="width: 47px; height: 47px" />
                </template>
              </el-table-column>
              <el-table-column
                prop="dname"
                label="菜品名称"
                width="170"
                align="center"
              >
              </el-table-column>
              <el-table-column width="100" label="菜品状态" align="center">
                <template v-slot="{ row }">
                  <el-button
                    size="mini"
                    v-show="row.status == 0"
                    type="success"
                    disabled
                    >在售</el-button
                  >
                  <el-button
                    size="mini"
                    v-show="row.status == 1"
                    type="info"
                    disabled
                    >待售</el-button
                  >
                </template>
              </el-table-column>
              <el-table-column label="操作" align="center">
                <template v-slot="{ row }">
                  <el-button
                    size="mini"
                    @click="agreeFn(row.did)"
                    v-show="row.dishelevel == 1"
                    type="primary"
                    >确认推荐</el-button
                  >
                  <el-button
                    size="mini"
                    @click="disAgreeFn(row.did)"
                    type="danger"
                    >拒绝推荐</el-button
                  >
                </template>
              </el-table-column>
            </el-table>
          </template>
        </div>
        <!-- 分页器 -->
        <div class="pager">
          <!-- 分页区域 -->
          <el-pagination
            @current-change="handleCurrentChangeFn"
            :current-page.sync="search.pagenum"
            :page-size.sync="search.pagesize"
            layout="total, prev, pager, next, jumper"
            :total="total"
          >
          </el-pagination>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import {
  getShopSugListAPI,
  getSugListAPI,
  agreeSugFoodAPI,
  disAgreeSugFoodAPI,
} from "@/api";
export default {
  name: "suggestion",
  // ==========================================以下是data==========================================
  data() {
    return {
      search: {
        pagenum: 1,
        pagesize: 4,
        keyword: "",
      },
      Type: "店铺推荐",
      // 总共的轮播图数
      total: 0,
      // 用于控制dialogform
      dialogFormVisible: false,
      // 用于控制表单label的宽
      formLabelWidth: "120px",

      tableData: [],
    };
  },
  // ==========================================以下是data==========================================
  methods: {
    // 通过推荐
    async agreeFn(foodID) {
      const { data: res } = await agreeSugFoodAPI({ id: foodID });
      if (res.code != 0) return this.$message.error(res.message);
      this.$message.success("推荐成功！");

      this.afterSug();
    },
    async disAgreeFn(foodID) {
      const { data: res } = await disAgreeSugFoodAPI({ id: foodID });
      if (res.code != 0) return this.$message.error("拒绝失败！");
      this.$message.success("拒绝成功！");
      this.afterSug();
    },
    // 推荐状态改变之后调用
    afterSug() {
      this.search.pagenum = 1;
      if (newV == "店铺推荐") {
        this.getShopSugList();
      } else {
        this.getSugList();
      }
    },
    // 获取店铺推荐的轮播图
    async getShopSugList() {
      const { data: res } = await getShopSugListAPI({
        page: this.search.pagenum,
        dname: this.search.keyword,
      });
      console.log(res);
      if (res.code != 0) return this.$message.error("获取菜品推荐列表失败！");
      this.tableData = res.data;
      this.total = res.count;
    },
    // 获取前台展示的菜品推荐列表
    async getSugList() {
      const { data: res } = await getSugListAPI({
        page: this.search.pagenum,
        dname: this.search.keyword,
        pagesize: this.search.pagesize,
      });
      if (res.code != 0 && res.code != 2)
        return this.$message.error("获取菜品推荐列表失败！");
      if (res.code == 1) {
        this.tableData = [];
        this.total = 0;
      } else {
        this.tableData = res.data;
        this.total = res.count;
      }
    },
    // 页码值发生了变化
    handleCurrentChangeFn(newPage) {
      // 为页码值赋值
      this.search.pagenum = newPage;
      // 重新发起请求
      if (this.swiperType == "店铺推荐") return this.getShopSugList();
      return this.getSugList();
    },
    // 搜索按钮
    searchFn() {
      if (this.search.keyword == "") {
        this.$message("请输入搜索关键词！");
      }
      this.search.pagenum = 1;
      this.getShopSugList();
    },
  },
  // ==========================================以下是mounted==========================================
  mounted() {
    this.getShopSugList();
  },
  // ==========================================以下是监视器==========================================

  watch: {
    Type: {
      immediate: true,
      handler(newV) {
        this.search.pagenum = 1;
        this.search.keyword = "";
        if (newV == "店铺推荐") {
          this.getShopSugList();
        } else {
          this.getSugList();
        }
      },
    },
  },
};
</script>

<style scoped>
.header {
  height: 60px;
  width: 80%;
  margin: 0 auto;
  text-align: center;
  line-height: 60px;
  font-size: 40px;
  font-family: "幼圆";
  font-weight: 600;
  color: rgb(84, 87, 87);
  background-color: rgb(197, 199, 205);
  border: rgb(183, 184, 187) solid 10px;
  border-radius: 10px;
}
.container {
  width: 90%;
  height: 485px;
  background-color: rgb(186, 193, 193);
  margin: 10px auto;
  border-radius: 10px;
  overflow: hidden;
}
.searchBox {
  width: 65%;
  height: 40px;
  margin: 10px auto;
  vertical-align: middle;
  overflow: hidden;
}
.searchInput {
  width: 75%;
  height: 30px;
  font-size: 15px;
  border-radius: 6px;
  border: rgb(242, 240, 210) solid 1px;
}
.searchBtn {
  width: 60px;
  height: 30px;
  font-size: 15px;
  color: rgb(67, 70, 72);
  background-color: rgb(169, 193, 225);
  border: rgb(127, 171, 228) solid 2px;
  border-radius: 6px;
  cursor: pointer;
  margin-left: 10px;
}
.searchBtn:active {
  background-color: rgb(248, 248, 248);
  border: rgb(232, 233, 235) solid 2px;
}
.addBtn {
  float: right;
  width: 100px;
  margin-left: 10px;
}

.listBox {
  width: 80%;
  height: 400px;
  background-color: rgb(244, 244, 245);
  border-radius: 5px;
  margin: auto;
  overflow: hidden;
}
.list {
  width: 98%;
  height: 360px;
  margin: 8px auto;
  overflow: hidden;
}
.pager {
  width: 65%;
  height: 30px;
  margin: auto;
  margin-top: -8px;
}
</style>
